.full-parent, html, body, view-controller, view-controller > .view-controller, lfx-view, lfx-view > .lfx-view, scroll-view > .scroll-view > .scroll-view, .lfx-player > video {
  width: 100%;
  height: 100%; }

* {
  -webkit-tap-highlight-color: transparent; }

main {
  display: inline-flex;
  flex-direction: column;
  width: 100%; }

view-controller {
  display: block; }

lfx-view > .lfx-view {
  overflow-y: auto;
  position: relative; }

scroll-view > .scroll-view {
  width: 96%;
  height: 450px;
  margin-left: 2%;
  overflow-x: hidden;
  border-bottom: 1px solid #cccccc; }
  scroll-view > .scroll-view > .scroll-view {
    display: flex;
    flex-direction: row;
    transform: translate3d(0, 0, 0); }

.lfx-player {
  width: 100%;
  height: 180px; }

.pagenav {
  width: 96%;
  margin-left: 2%;
  border-bottom: 1px solid #cccccc; }
  .pagenav .pagenav__item {
    height: 35px;
    text-align: center; }
    .pagenav .pagenav__item.iron-selected {
      color: #00b3ee;
      transition: all 0.3s ease; }


